import React, {useState,  useEffect} from 'react'

const Blog = ({ blog, removeFunction, likeBlog}) => {

  const blogStyle = {
    paddingTop: 10,
    paddingLeft: 2,
    border: 'solid',
    borderWidth: 1,
    marginBottom: 5
  }

  const [visible, setVisible] = useState(false)
  const [removable, setRemovable] = useState(false)

  const showWhenRemovable = { display: removable ? '' : 'none' }
  const hideWhenVisible = { display: visible ? 'none' : '' }
  const showWhenVisible = { display: visible ? '' : 'none' }

  useEffect(() => {
    checkRemovability()
  }, [])

  const toggleVisibility = () => {
    setVisible(!visible)
  }

  const checkRemovability = () => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogappUser')
    if(loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      console.log(blog.user.username)
      console.log(user)
      console.log(user.username)
      console.log(blog.user.username===user.username)
      if(blog.user.username === user.username) {
        setRemovable(true)
      }
      else{
        setRemovable(false)
      }
    }
  }

  return (
    <div style={blogStyle} className = 'blog'>
      <div>{blog.title}</div>
      <div style={hideWhenVisible}>
        <button onClick={toggleVisibility}>Show details</button>
      </div>
      <div style={showWhenVisible}>
        <button onClick={toggleVisibility}>Hide</button><br></br>
        <div>{blog.author}</div>
        <div>{blog.url}</div>
        <div>{blog.likes} <button title={blog.title} likes={blog.likes} author={blog.author} url={blog.url} id={blog.id} onClick={likeBlog}>Like</button></div>
      </div>
      <div style={showWhenRemovable}>
        <button name = {blog.title} value={blog.id} onClick={removeFunction}> Delete</button>
      </div>
    </div>
  )
}

export default Blog